@import url("./main.css");
/* ========== Home ========== */

.home_page {
  width: 100%;
}
.home_page section {
  width: 100%;
}
.home_page section h1 {
  text-align: center;
}
.home_page section .content {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;
}
/* left */
.home_page section .content .ctn-left {
  width: 40%;
  text-align: center;
}
.home_page section .content .ctn-left p {
  margin-bottom: 2em;
  font-size: 0.8rem;
}
.home_page section .content .ctn-left h2 {
  font-size: 2rem;
  margin: 0 0 1rem;
}
.home_page section .content .ctn-left h2 .gradient {
  line-height: 1rem;
  background: linear-gradient(100deg, rgb(44, 161, 240), rgb(243, 43, 183));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.home_page section .content .ctn-left h2 .gradient::after {
  content: "";
  animation: text 2.5s linear infinite;
}
@keyframes text {
  0% {
    content: "Hello test";
  }
  100% {
    content: "Test success";
  }
}

/* right */
.home_page section .content .ctn-right {
  width: 40%;
}
.home_page section .content .ctn-right img {
  width: 100%;
}

/* ========== RESPORNSIVE ========== */

@media only screen and (max-width: 768px) {
  /*  */
  .home_page section .content {
    flex-wrap: wrap-reverse;
    flex-direction: column-reverse;
    width: 100%;
  }
  .home_page section .content .ctn-left {
    width: 80%;
  }

  .home_page section .content .ctn-right {
    margin-bottom: 2em;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .home_page section .content .ctn-right img {
    width: 300px;
  }
}

@media only screen and (max-width: 576px) {
  .home_page {
    width: 100%;
  }
  .home_page section .content {
    flex-wrap: wrap-reverse;
    flex-direction: column-reverse;
    width: 100%;
  }
  /* left */
  .home_page section .content .ctn-left {
    width: 100%;
  }
  /* right */
  .home_page section .content .ctn-right {
    margin: auto;
    margin-bottom: 1.5em;
    width: 70%;
  }
  .home_page section .content .ctn-right img {
    width: 100%;
  }
}
