@import url("./main.css");
/* ========== COURSE ========== */

.course {
  margin: auto;
  width: 90%;
}

.course .course-list-main {
  margin: 2em auto;
  max-width: 100%;
  /* background-color: bisque; */
}

.course .course-list-main .course-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 3rem 2rem;
  z-index: 1;
}

.course .course-list-main .course-list .card {
  width: 220px;
  height: auto;
  overflow: hidden;
  border-radius: 1.2rem;
  background-color: #2a303c;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.course .course-list-main .course-list .card .card-body {
  width: 100%;
}

.course .course-list-main .course-list .card .header {
  overflow: hidden;
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9;
  background-color: var(--color-white);
}
.course .course-list-main .course-list .card .header .bagde {
  position: absolute;
  content: "";
  top: 5px;
  right: 5px;
  padding: 0 15px 0 10px;
  font-family: Romnea;
  border-radius: 0.3rem 1rem 0.3rem 0.3rem;
  color: red;
  /* border: 2px solid #fff; */
  background-color: var(--color-white-90);
  z-index: 5;
}
.course .course-list-main .course-list .card .header .header-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: center;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.4rem;
  transition: var(--transition);
  z-index: 1;
}
.course .course-list-main .course-list .card .header .header-img img {
  width: 100%;
  /* height: 100%; */
}
.course .course-list-main .course-list .card .header .header-img:hover {
  transform: scale(1.1);
}
