/* ===== CARD-BODY ===== */
.course .course-list-main .course-list .card .card-body {
  position: relative;
  width: 100%;
  height: 150px;
}
.course .course-list-main .course-list .card .card-body .title {
  padding: 0.2rem;
  width: 100%;
}
.course .course-list-main .course-list .card .card-body .title h3 {
  color: var(--color-white);
  text-align: center;
  font-size: 18px;
  width: 100%;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  opacity: 0.8;
}
.course .course-list-main .course-list .card .card-body .title p {
  text-align: center;
  width: 100%;
  font-size: 0.8rem;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  opacity: 0.8;
}

.course .course-list-main .course-list .card .card-body .meta-deteil {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  margin-top: 1rem;
  padding: 0.5rem 0.4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.course .course-list-main .course-list .card .card-body .meta-deteil .prifile {
  display: flex;
  align-items: center;
}
.course
  .course-list-main
  .course-list
  .card
  .card-body
  .meta-deteil
  .prifile
  .pf-icon {
  margin-right: 5px;
  width: 34px;
  height: 34px;
  /* border: 2px solid #ffffff80; */
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.course
  .course-list-main
  .course-list
  .card
  .card-body
  .meta-deteil
  .prifile
  .pf-icon
  img {
  width: 100%;
  /* height: 100%; */
}
.course
  .course-list-main
  .course-list
  .card
  .card-body
  .meta-deteil
  .prifile
  .pf-name {
  font-size: 16px;
  font-weight: 500;
}

.course .course-list-main .course-list .card .card-body .meta-deteil .price p {
  letter-spacing: 1px;
  color: red;
}
.course
  .course-list-main
  .course-list
  .card
  .card-body
  .meta-deteil
  .price
  p
  span {
  color: yellow;
}

/* btn */
.learn-more {
  width: 100%;
  padding: 10px 0;
  display: flex;
  justify-content: center;
}
.btn-lm {
  padding: 10px 15px;
  border-radius: 1rem;
  background-color: var(--color-primary);
  cursor: pointer;
}
.btn-lm:hover {
  color: var(--color-white);
  background-color: var(--color-success);
}

/* loader */

/* ========== RESPORNSIVE ========== */

@media only screen and (max-width: 992px) {
  .course .course-list-main .course-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem 1rem;
  }
  .course .course-list-main .course-list .card {
    margin: auto;
    width: 240px;
  }
}

@media only screen and (max-width: 768px) {
  .course .course-list-main .course-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .course .course-list-main .course-list .card {
    margin: auto;
    width: 260px;
  }
}

@media only screen and (max-width: 576px) {
  .course .course-list-main .course-list {
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .course .course-list-main .course-list .card {
    margin: auto;
    width: 250px;
  }
}
