@font-face {
  font-family: Romnea;
  src: url("../fonts/Romnea-Italic.ttf") format("truetype");
}
:root {
  --color-light: #fafbff;
  --color-light-75: rgba(255, 255, 255, 0.75);
  --color-light-25: rgba(255, 255, 255, 0.25);
  --color-white: #fff;
  --color-white-90: rgba(255, 255, 255, 0.9);
  --color-white-text: #b5becc;
  --color-black: #000;
  --color-dark: #1f2022;
  --color-primary: #6c63ff;
  --color-success: #00bf8e;
  --color-warning: #f7c94b;
  --color-danger: #f75842;
  --color-bg: #2a303c;
  /* --color-bg: #1f2641; */
  --color-bg-30: rgba(31, 38, 65, 0.3);
  --color-bg-50: rgba(31, 38, 65, 0.5);
  --color-bg-70: rgba(31, 38, 65, 0.75);
  --color-bg1: #2e3267;
  --color-bg2: #424890;
  --color-bg3: rgb(14, 19, 27);

  --color-blur: rgb(255, 237, 237, 0.37);
  --blur: blur(55px);

  --transition: all 400ms ease;
  --transition-text: all 200ms ease;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
}

body {
  scroll-behavior: smooth;
  width: 100%;
  font-family: Romnea;
  color: var(--color-white-text);
  background-color: var(--color-bg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.bodyDark {
  color: #fff;
  background-color: #323773;
  /* background-color: #041e44; */
}

.container {
  margin: 0 auto;
  padding-top: 100px;
  width: 100%;
  max-width: 80%;
}
header {
  padding: 12px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99999;
  backdrop-filter: blur(35px);
  transition: var(--transition);
}
.headerShow {
  box-shadow: 0 0 15px 1px var(--color-bg-70);
  background: linear-gradient(
    to top,
    rgba(31, 32, 34, 0.66),
    rgba(46, 50, 103, 0.75)
  );
  transition: var(--transition);
}
header .navBar {
  margin: 0 auto;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
header .navBar .logo h1 {
  font-size: 1.4rem;
}
header .navBar .logo a {
  color: var(--color-white);
}
/* ========== Navbar ========== */
header .navBar .menu {
  display: block;
}
header .navBar .menu .nav_menu {
  display: flex;
}
header .navBar .menu .nav_menu li {
  margin: 0 1.4rem;
}
header .navBar .menu .nav_menu li a {
  font-size: 1.2rem;
  font-weight: 400;
  font-family: Romnea;
  color: var(--color-light-75);
  transition: var(--transition-text);
}
header .navBar .menu .nav_menu li a:hover {
  color: var(--color-light);
}
header .navBar .menu .nav_menu li .active {
  color: var(--color-light);
  position: relative;
}
header .navBar.menu .nav_menu li .active::after {
  position: absolute;
  content: "";
  bottom: -4px;
  left: 0;
  width: 100%;
  height: 3px;
  border-radius: 2px;
  background-color: var(--color-danger);
}
header .navBar .menu #copyright {
  display: none;
}
header .navBar .menu #copyright p {
  font-size: 0.7rem;
  text-transform: uppercase;
}

/* ========== BUTTON ========== */
.btn-start {
  transition: var(--transition-text);
  animation: modleBtn 1s infinite ease;
}
@keyframes modleBtn {
  0%,
  100% {
    transform: scale(1);
  }
  85% {
    transform: scale(1.08);
    background: linear-gradient(100deg, rgb(243, 43, 183), rgb(44, 161, 240));
  }
}
.btn {
  cursor: pointer;
  border-radius: 1.2rem;
  background: linear-gradient(100deg, rgb(44, 161, 240), rgb(243, 43, 183));
}

.btn a {
  width: 100%;
  height: 100%;
  display: block;
  padding: 10px 20px;
  font-size: 1rem;
  color: var(--color-light);
}
.btn:hover {
  /* background: linear-gradient(100deg, rgb(243, 43, 183), rgb(44, 161, 240)); */
}

header .navBar .menu #closeBtn {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  /* right: 0; */
  width: 33px;
  height: 33px;
  border-radius: 20%;
  cursor: pointer;
  background: url("../images/close.png") var(--color-light);
  background-position: center;
  /* background-repeat: no-repeat; */
  background-size: cover;
}
header .navBar #openBtn {
  display: none;
  width: 33px;
  height: 33px;
  background: url("../images/open.png") var(--color-light);
  background-position: center;
  /* background-repeat: no-repeat; */
  background-size: cover;
  border-radius: 20%;
  cursor: pointer;
}

/* ========== Footer ========== */
footer {
  margin-top: 2em;
  width: 100%;
  background-color: var(--color-bg1);
}
footer .copyright {
  padding: 1.5rem 0;
  margin: auto;
  width: 90%;
  display: flex;
  justify-content: center;
}
footer .copyright p {
  text-align: center;
}

/* ========== RESPORNSIVE ========== */

@media only screen and (max-width: 768px) {
  .container {
    margin: 0 auto;
    padding-top: 100px;
    max-width: 85%;
  }
  header .navBar {
    width: 90%;
  }
}

@media only screen and (max-width: 576px) {
  .container {
    margin: 0 auto;
    padding-top: 100px;
    max-width: 90%;
  }

  header .navBar .toggleBtn {
    display: block;
  }
  /* ========== menu ========== */
  header .navBar {
    width: 90%;
  }
  header .navBar .menu {
    overflow: hidden;
    padding: 15px;
    position: fixed;
    top: 0;
    right: -100%;
    width: 70%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    z-index: 100;
    backdrop-filter: blur(100px);
    transition: var(--transition);
    background: var(--color-primary);
  }
  header .navBar .menuShow {
    right: 0;
  }
  header .navBar .menu .nav_menu {
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }
  header .navBar .menu .nav_menu li {
    margin: 1.2rem 0;
    border-bottom: 2px solid var(--color-light-25);
  }
  header .navBar .menu .nav_menu li a {
    margin-left: 1.5rem;
    font-size: 1.2rem;
    color: var(--color-light-75);
  }
  header .navBar .menu .nav_menu li .active::before {
    position: absolute;
    content: "";
    top: 50%;
    left: -60px;
    transform: translate(0, -50%);
    width: 50px;
    height: 3px;
    background-color: var(--color-white);
  }
  header .navBar .menu .nav_menu li .active::after {
    position: absolute;
    content: "";
    top: 50%;
    left: -15px;
    transform: translate(0, -50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: var(--color-white);
    box-shadow: 0 0 10px 1px #fff;
  }
  /* btn */
  header .navBar #openBtn {
    display: block;
  }
  header .navBar .menu #closeBtn {
    display: block;
  }
  header .navBar .menu #copyright {
    display: block;
    text-align: center;
  }
  /* header .navBar .menu #closeBtn img {
        width: 100%;
        height: 100%;
      } */
  /* ========== Footer ========== */
  footer {
    margin-top: 3em;
  }
}
