/* Private home page */
/* ///////////////////////////////////////// */
.pv-btn {
  border-radius: 1.2rem;
  padding: 10px 20px;
  cursor: pointer;
  background: linear-gradient(100deg, rgb(243, 43, 183), rgb(44, 161, 240));
}
.pv-btn:focus {
  animation: btn 0.2s ease;
}
@keyframes btn {
  0% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

section {
  margin: 2em auto;
  width: 80%;
}
section .private {
  width: 100%;
}
section .private h1 {
  text-align: center;
  font-size: 2rem;
}

section .pv-content {
  width: 100%;
}
section .pv-content h1 {
  margin-bottom: 1em;
  font-size: 1.5rem;
}

